import React from 'react'
import '../styles/Dev.css'
import {Link} from 'react-router-dom';

const pictures = [
    {
      source:
        "http://leofrati.fr/images/oplus-th-homepage.jpg",
      content: {
        name: "O+ Urbanistes",
        link:"/post/oplus"
      }
    },
    {
      source:
      "http://leofrati.fr/images/jolib-th-homepage.jpg",
      content: {
        name: "Jolibois",
        link:"/post/jolibois"
      }
    },
    {
      source:
      "http://leofrati.fr/images/ia-th-homepage.jpg",
      content: {
        name: "Isabelle Adragna",
        link:"/post/adragna"
      }
    },
  ];

  const FolioItems = ({ imageSource, content }) => {
    return (
      <div className="folio-container-single rev-block">
        <div className="folioSingle">
          <Link to={content.link}><img src={imageSource} alt="Folio-images" draggable="false" /></Link>
        </div>
        <div className="infos">
          <h4>{content.name}</h4>
        </div>
      </div>
    );
  };
  
  export const Dev = () => { 
    return (
     <div className="folio-container rev-block">
     <div className="folio-content-container">
     <div className="folio-title-container"><div className="rev-block"><h1 class="playfair">Développement Web</h1></div></div>
      <div id="folio" className="Folio">
        {pictures.map((item, index) => {
          return (
            <FolioItems key={index} imageSource={item.source} content={item.content} />
          );
        })}
      </div>
     </div>
     </div>
    );
  };

export default Dev;