import React from 'react'
import '../styles/Dev.css'
import {Link} from 'react-router-dom';

const pictures = [
    {
        source:
          "http://leofrati.fr/images/esdexp-th-homepage.jpg",
        content: {
          name: "Expérience ESD",
          link:"/post/ESDexp"
        }
      },
    {
      source:
        "http://leofrati.fr/images/vacui-th-homepage.jpg",
      content: {
        name: "Horror Vacui",
        link:"/post/horrorvacui"
      }
    },
    {
      source:
      "http://leofrati.fr/images/meteo-th-homepage.jpg",
      content: {
        name: "Forecast",
        link:"/post/forecast"
      }
    },
    {
      source:
      "http://leofrati.fr/images/ami-th-homepage.jpg",
      content: {
        name: "Citroën Ami",
        link:"/post/ami"
      }
    },
    {
        source:
        "http://leofrati.fr/images/musee-th-homepage.jpg",
        content: {
          name: "Musée Home",
          link:"/post/musee"
        }
      },
  ];

  const FolioItems = ({ imageSource, content }) => {
    return (
      <div className="folio-container-single rev-block">
        <div className="folioSingle">
          <Link to={content.link}><img src={imageSource} alt="Folio-images" draggable="false" /></Link>
        </div>
        <div className="infos">
          <h4>{content.name}</h4>
        </div>
      </div>
    );
  };
  
  export const Ui = () => { 
    return (
     <div className="folio-container rev-block">
     <div className="folio-content-container">
     <div className="folio-title-container"><div className="rev-block"><h1 class="playfair">UI Design</h1></div></div>
      <div id="folio" className="Folio">
        {pictures.map((item, index) => {
          return (
            <FolioItems key={index} imageSource={item.source} content={item.content} />
          );
        })}
      </div>
     </div>
     </div>
    );
  };

export default Ui;